.button {
  padding: 20px 40px;
  width: 66%;
  height: 56px;
  font-size: 18px;
  background: var(--color-primary);
  border-radius: 18px;
  border: none;
  color: var(--color-white);
  transition: 0.3s;
  cursor: pointer;
  position: absolute;
  bottom: 60px;
}
.button:hover {
  background-color: var(--color-primary-dark);
}
.button:disabled {
  background-color: var(--color-default);
  cursor: default;
}
.button--return {
  bottom: 60px;
}
.button a {
  text-decoration: none;
  color: var(--color-white);
}
@media (max-width: 600px) {
  .button {
    width: 81%;
  }
}
@media (max-width: 425px) {
  .button {
    bottom: 80px;
  }
}/*# sourceMappingURL=Button.css.map */