.app-container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 588px;
  height: 811px;
  background: var(--color-white);
  border-radius: 20px;
  margin: 0 auto;
  padding: 40px 100px;
  box-sizing: border-box;
  margin-top: 60px;
  box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.16), 0px 0px 0px 1px rgba(0, 0, 0, 0.06);
}
.app-container header {
  text-align: center;
}
.app-container h1 {
  font-size: 38px;
  line-height: 0;
}
.app-container h2 {
  font-size: 40px;
  text-align: center;
  margin-top: 60px;
}
.app-container p {
  font-size: 20px;
  text-align: center;
}
@media (max-width: 600px) {
  .app-container {
    max-width: 422px;
    padding: 40px;
  }
}
@media (max-width: 425px) {
  .app-container {
    max-width: 422px;
    padding: 40px;
    margin-top: 0;
    border-radius: 0;
    box-shadow: none;
  }
}/*# sourceMappingURL=OrderingPage.css.map */