.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.form .form__label {
  position: absolute;
  margin-top: 17px;
  left: 120px;
  color: var(--color-grey);
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
}
.form .form__label--slices {
  left: 20px;
}
.form .form__label--diameter {
  left: 218px;
}
.form .form__input-container {
  display: flex;
  gap: 8px;
  position: relative;
}
.form .form__input {
  box-sizing: border-box;
  width: 100%;
  height: 56px;
  border: 1px solid var(--color-grey);
  border-radius: 18px;
  margin-bottom: 14px;
  padding: 6px 20px 0;
  transition: 0.3s;
  font-size: 16px;
  color: var(--color-on-surface);
  box-shadow: 4px 4px 8px #f5f5f5, -4px -4px 8px #f5f5f5;
}
.form .form__input:hover {
  border: 1px solid var(--color-on-surface);
}
.form .form__input:hover + .form__label {
  color: var(--color-on-surface);
}
.form .form__input:focus-visible {
  border: 1px solid var(--color-primary);
  outline: none;
}
.form .form__input:not(:-moz-placeholder-shown) + .form__label {
  transform: translate3d(0, -25px, 0) scale(0.8);
  color: var(--color-grey);
  background-color: var(--color-white);
  padding: 3px;
}
.form .form__input:not(:placeholder-shown) + .form__label {
  transform: translate3d(0, -25px, 0) scale(0.8);
  color: var(--color-grey);
  background-color: var(--color-white);
  padding: 3px;
}
.form .form__input:focus + .form__label {
  color: var(--color-primary);
  transform: translate3d(0, -25px, 0) scale(0.8);
  background-color: var(--color-white);
  padding: 3px;
}
@media (max-width: 600px) {
  .form .form__label {
    left: 60px;
  }
}/*# sourceMappingURL=DishForm.css.map */