.range {
  position: relative;
}
.range p {
  font-size: 12px;
  margin-top: 20px;
}
.range label {
  font-size: 14px;
}
.range .range__container {
  display: flex;
  justify-content: space-between;
  line-height: 0;
  color: var(--color-grey);
}
.range .range__label {
  position: absolute;
  left: 20px;
  top: -8px;
  font-size: 13px;
  color: var(--color-grey);
}
.range input[type=range] {
  -webkit-appearance: none;
  height: 24px;
  margin: 10px 0;
  width: 100%;
}
.range input[type=range]::-webkit-slider-runnable-track {
  width: 300px;
  height: 5px;
  background: var(--color-white);
  border: 1px solid var(--color-grey);
  border-radius: 50px;
}
.range input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  background: var(--color-primary);
  margin-top: -10px;
}
.range input[type=range]:focus + .range__label {
  color: var(--color-primary);
}/*# sourceMappingURL=Range.css.map */